body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
html {
  font-size: 62.5%;
  font-family: "DM Sans", sans-serif;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
main {
  width: 100%;
  height: auto;
  background-color: #ececec;
  min-width: 320px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.ho {
  color: #2cbdee;
}
.landing {
  display: flex;
  justify-content: center;
  flex-direction: column;
  position: relative;
  width: 100%;
  min-width: 320px;
  min-height: 334px;
  height: 334px;
  text-align: center;
  background: linear-gradient(207.8deg, #201E1C 16.69%, #F7931A 100%);
}
.landing img {
  width: 150px;
  height: 24px;
  margin-top: 60px;
  align-self: center;
}
.landing .title--container {
  width: 90%;
  min-width: 288px;
  max-width: 900px;
  height: 288px;
  margin-top: 50px;
  text-align: center;
  align-self: center;
  color: #FFFFFF;
}
.landing .title--container h1 {
  font-weight: bold;
  font-size: 2.4rem;
  line-height: 2.6rem;
  text-align: center;
}
.landing .title--container p {
  margin-top: 25px;
  font-size: 1.4rem;
  font-weight: 500;
  color: #FFE9D5;
}
.landing .title--container a {
  position: absolute;
  top: calc(100% - 24px);
  left: calc(50% - 114.5px);
  top: 270px;
  display: block;
  margin-top: 35px;
  background-color: #FAF8F7;
  padding: 12px 16px;
  text-decoration: none;
  color: #201E1C;
  border-radius: 5px;
  width: 229px;
  height: 48px;
  border: none;
  font-size: 1.4rem;
  font-weight: bold;
  box-shadow: 0px 4px 8px rgba(89, 73, 30, 0.16);
}
.landing .title--container span {
  display: inline-block;
  width: 13px;
  height: 8px;
  margin-left: 10px;
  background-image: url(/static/media/down-arrow.94e075cf.svg);
}
@media (min-width: 768px) {
  .landing .title--container h1 {
    font-size: 2.8rem;
  }
  .landing .title--container p {
    font-size: 1.8rem;
  }
  .landing .title--container a {
    width: 300px;
    height: 60px;
    padding: 18px 22px;
    font-size: 2rem;
    left: calc(50% - 140px);
  }
}

