.product--card {
  width: 85%;
  min-width: 288px;
  max-width: 400px;
  min-height: 150px;
  margin: 15px auto;
  background-color: #282623;
  border-radius: 5px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.16);
  padding: 15px;
  text-align: left;
  font-family: "DM Sans", sans-serif;
}
.product--card .product--card__title {
  margin-bottom: 15px;
