}
.product--card .product--card__body {
  font-size: 1.4rem;
  font-weight: 500;
  Line-height: 1.8rem;
  color: #808080;
}
.product--card .clock {
  background: url(/static/media/clock.7f17853a.svg) center / cover no-repeat;
}
.product--card .eyes {
