footer {
  display: flex;
  width: 100%;
  height: 150px;
  background-color: #F7931A;
  font-family: "DM Sans", sans-serif;
}
footer .batata-logo {
  background-image: url(/static/media/logo-footer.4e3c21d3.svg);
  background-repeat: no-repeat;
  width: 64px;
  height: 50px;
  margin: auto;
}
footer .left,
footer .right {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50%;
}
footer .right {
  flex-direction: column;
}
footer .right p {
  position: absolute;
  margin-top: 90px;
  color: #e9e9e9;
  font-family: "DM Sans", sans-serif;
  font-size: 1.2rem;
}
footer .left ul {
  font-size: 1.4rem;
  font-weight: 500;
  line-height: 1.8rem;
  list-style: none;
}
footer .left ul li {
  margin: 10px;
  text-align: left;
}
footer .left ul a {
  text-decoration: none;
  color: white;
}
footer .left ul a:hover {
  color: #FFE9D5;
}
@media (min-width: 768px) {
  footer .batata-logo {
    width: 80px;
    height: 64px;
    background-size: cover;
  }
  footer .left ul {
    display: flex;
  }
  footer .left ul li {
    margin: 0 20px;
    font-size: 1.6rem;
  }
  footer .right p {
    font-size: 1.4rem;
  }
}

